.bubble-slider {
  display: block;
  width: 100%;
  margin: 20px auto;
  padding: 20px;
  font-size: 16px;
}
.bubble-slider-wrap {
  position: relative;
  margin: 55px auto;
  display: block;
  width: 100%;
  height: 50px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.bubble-slider-wrap::before,
.bubble-slider-wrap::after {
  content: '';
  display: table;
}
.bubble-slider-wrap::after {
  clear: both;
}
.bubble-slider-track {
  position: absolute;
  left: 40px;
  right: 40px;
  top: 25px;
  height: 2px;
  background: #e5e5e5;
}
.bubble-slider-thumb {
  position: absolute;
  top: -25px;
  width: 50px;
  height: 50px;
  background: #595959;
  border-radius: 50%;
  cursor: ew-resize;
}
.bubble-slider-bubble {
  position: absolute;
  top: -30px;
  left: 25px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.bubble-slider-bubble span {
  padding: 5px 15px;
  display: inline-block;
  font-size: 25px;
  color: #bdc3c3;
  background: #343a3a;
  border-radius: 5px;
}
.bubble-slider-bubble-arrow {
  background: #343a3a;
  position: absolute;
  left: 50%;
  bottom: -3px;
  margin-left: -5px;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.bubble-slider-minus {
  position: relative;
  float: left;
}
.bubble-slider-plus {
  position: relative;
  float: right;
}
.bubble-slider-minus,
.bubble-slider-plus {
  top: 10px;
  width: 30px;
  height: 30px;
  color: #595959;
  font-size: 18px;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
  cursor: pointer;
}
.bubble-slider-minus span {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.bubble-slider-plus span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
